import React from 'react'
import Link from 'gatsby-link'
import Container from "../layouts/container";
import Header from "../components/header";
import nba from '../nba.jpg';
import megs from '../megs.png';
import scully from '../scully.png';
import styles from "./work.module.css";



const links = [`Home`, `CV`, `Contact`];

const Work = (props) => (
  <Container>
    <h1> Work </h1>
    <Header links={links} />

    <h2> Professional </h2>

    <div className={styles.workRow}>
      <a href="https://stats.nba.com" title="NBA.com/stats"> <img className={styles.workIcon} src={nba} alt="NBA Logo" /> </a>
      <div>
        From November 2015 to January 2018, I was part of the NBA's development team. During my time there, I learned a lot about javascript, namely AngularJS, but I also worked with React and Vue. This job was where I really learned how to do anything web dev related as it was my first position out of college. 
        The NBA had an agile environment, so we had weekly stand ups where bug fixes and new features were assigned through JIRA. My day to day consisted of implementing these fixes/featuers on sites such as <a href="https://stats.nba.com"> NBA.com/stats </a>, <a href="wnba.com"> WNBA.com </a>, and many other NBA properties.
      </div>
    </div>

    <div className={styles.workRow}>
      <a href="https://www.megsradio.fm/" title="MegsRadio.fm"> <img className={styles.workIcon} src={megs} alt="MegsRadio Logo" /> </a>
      <div>
        I worked as a Web Developer and UI/UX Designer for MegsRadio from May 2014 to May 2015. I started in the summer before my senior year, and this position ultimately became my senior project under the guidance of Professor Doug Turnbull at Ithaca College. 
        Megs Radio is an ad-free, non-profit, personlized Internet radio system. The primary goal is to promote the work of local artists by contextualizing their music with songs by more well-known artists.
        During my time at Megs, I researched similar apps, created paper prototypes, user tested and iterated through designs in Photoshop, Sketch, Proto.io, InVision, and Marvel, and then started developing the base player application in AngularJS.

      </div>
    </div>

    <h2> Personal </h2>

    <div className={styles.workRow}>
      <a href="https://issuu.com/rawaidakhtar/docs/sd_001.7_final_v2__2_" title="Read Scully's Digest on issuu"> <img className={styles.workIcon} src={scully} alt="Scully's Digest cover" /> </a>
      <div>
        Scully's Digest is a personal project that I took on recently. I've always loved comics and visual art and decided to try my hand at making my own publication with my friends, which was handed out at a house where we frequently throw concerts. Scully's Digest is an on going work in progress with no set schedule or ideas, just something to experiment with. You can click the image above to read the first issue!
      </div>
    </div>
    

  </Container>
);


export default Work;


